.abril-fatface-regular {
    font-family:Georgia, 'Times New Roman', Times, serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .sedan-regular {
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 400;
    font-style: normal;
  }
  
  
  
  body{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    background-color: beige;
  }
  
  form {
    width: 90%;
    max-width: 700px;
    padding: 20px;
    background-color: rgb(222, 194, 142);
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
  form h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #f24c0b;
    font-family: "Abril Fatface", serif;
    font-weight: 400;
    font-style: normal;
  }
  
  form div {
    margin-bottom: 15px;
  }
  
  form label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    margin-left: 10px;
    color: #2b2d42;
    font-family: "Sedan", serif;
    font-weight: 400;
    font-style: normal;
  }
  
  form input,
  form select {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-left: 10px;
  }
  
  form input[type="checkbox"] {
    width: auto;
    margin-left: 10px;
  }
  
  form span {
    color: red;
    font-size: 12px;
    margin-left: 10px;
  }
  
  form button {
    display: block;
    width: 100%;
    padding: 12px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 20px;
    font-family: "Abril Fatface", serif;
    font-weight: 400;
    font-style: normal;
  }
  
  form button:hover {
    background-color: #0056b3;
  }
  
  form input.error,
  form select.error {
    border: 2px solid red;
  }
  
  .row1,
  .row2,
  .row3,
  .row4,
  .row5 {
    display: flex;
    gap: 15px;
  }
  
  .row1 div,
  .row2 div,
  .row3 div,
  .row4 div,
  .row5 div {
    flex: 1;
  }